<template>
  <div class="container">
    <div class="d-flex justify-center col-md-12">
      <div class="d-flex align-top col-md-11 flex-wrap">
        <div class="d-flex col-md-12">
          <div>
            <h3 class="display-1 font-weight-bold title-color  mb-3">
              MY PORTFOLIO <ModalFilters />
            </h3>
            <p>
              Using modern development libraries/frameworks like React.JS, Redux,
              Vue.JS, Node.JS, and others. Check out my latest software
              development portfolio projects.
            </p>
          </div>
        </div>

        <template v-for="(item, i) in projects">
          <div class="col-md-4 col-sm-6 col-xs-12" :key="i">
            <div class="pa-1">
              <v-card max-width="350" min-width="250">
                <div class="lightbox">
                  <v-img
                    height="200"
                    position="top center"
                    :src="require(`@/assets/${item.icon}`)"
                  >
                    <v-row
                      align="end"
                      class="lightbox white--text pa-2 fill-height"
                    >
                      <a :href="item.link" target="_blank">
                        <v-card-title class="project-title pb-1">{{
                          item.title
                        }}</v-card-title>
                      </a>
                    </v-row>
                  </v-img>
                </div>

                <v-card-text>
                  <!-- <div class="my-4 subtitle-1 black--text">
                    Description
                  </div> -->

                  <div>
                    {{ item.desc }}
                  </div>
                </v-card-text>

                <v-divider class="mx-4"></v-divider>
                <v-card-text class="">
                  <!-- <div class="my-4 subtitle-1 black--text">
                    Technologies
                  </div> -->
                  <div class="max-tech-box">
                    <v-chip
                      :ripple="false"
                      v-bind:style="item.style[ti]"
                      v-for="(tech, ti) in item.tech"
                      :key="ti"
                      >{{ tech }}</v-chip
                    >
                  </div>
                </v-card-text>
              </v-card>
            </div>
          </div>
        </template>
        <v-alert
          v-if="projects.length == 0"
          icon="info"
          prominent
          text
          type="info"
        >
          Sorry, no results found! try a different search selection.
        </v-alert>
      </div>
    </div>
  </div>
</template>

<script>
import ModalFilters from "../components/portfolio/ModalFilters";
export default {
  components: {
    ModalFilters
  },
  data: () => ({
    projects: []
  }),
  mounted() {
    this.generateData();
  },
  watch: {
    $route() {
      this.generateData();
    }
  },
  methods: {
    generateRandomInt() {
      return Math.floor(Math.random() * Math.floor(255));
    },
    generateData() {
      const params = this.$route.params.filters;
      const projects = JSON.parse(JSON.stringify(this.$store.state.projects));
      const filteredProjects = [];
      for (const x in projects) {
        const item = projects[x];
        item.style = [];
        if (params && JSON.parse(params).length != 0) {
          let state = true;
          if (JSON.parse(params).some(e => item.tech.includes(e))) {
            state = false;
          }
          if (state) {
            continue;
          }
        }
        for (const y in item.tech) {
          const tech = item.tech[y];
          const first = this.generateRandomInt();
          const second = this.generateRandomInt();
          const third = this.generateRandomInt();
          item.style.push({
            backgroundColor: `rgba(${first}, ${second}, ${third}, 0.2)`,
            border: `1px solid rgba(${first}, ${second}, ${third}, 1)`,
            margin: "2px",
            fontSize: "12px"
          });
          tech;
        }
        filteredProjects.push(item);
      }
      this.projects = filteredProjects;
    }
  }
};
</script>

<style lang="scss">
@import "@/css/variable.scss";
.v-application.portfolio .container.pa-4 {
  padding: 0px !important;
  margin: 0px !important;
  width: 100% !important;
  max-width: 100% !important;
}
.portfolio #mainDrawer .deep-purple--text.text--accent-4 {
  color: $blue--color;
  caret-color: $blue--color;
}
</style>

<style lang="scss" scooped>
@import "@/css/variable.scss";
.portfolio {
  .container {
    padding: 0px !important;
    .header-banner {
      // height: 137px;
      width: 100%;
      // background-color: #007791;
      //color: #fff;
    }
  }

  .title-color {
    color: $blue--color;
  }

  .lightbox {
    box-shadow: 0 0 20px inset rgba(0, 0, 0, 0.2);
    background-image: linear-gradient(
      to top,
      rgb(255, 255, 255) 0%,
      #0000 72px
    );
  }

  .page-color {
    background-color: $blue--color;
  }
  header {
    background-color: $blue--color;
    border-color: $blue--color;
    color: #fff !important;

    .v-tab {
      color: rgba(255, 255, 255, 0.84) !important;
    }
    button,
    i,
    .v-toolbar__title,
    .v-tab--active {
      color: #fff !important;
    }
  }

  .max-tech-box {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
</style>
